  @media (max-width: 48em) {
    #index .collections__list {
      margin-bottom: -20px; } }

#index .collections__item {
  overflow: hidden;
  width: calc(50% - 15px);
  margin-bottom: 30px; }
  @media (max-width: 48em) {
    #index .collections__item {
      width: 100%;
      margin-bottom: 20px; } }
  #index .collections__item a {
    position: relative;
    display: block;
    text-decoration: none; }
    #index .collections__item a .image img {
      transition: transform 0.3s ease;
      width: 100%; }
    #index .collections__item a .label {
      position: absolute;
      z-index: 1;
