      left: 30px;
      display: inline-block;
      padding: 5px 40px 3px;
      background-color: #1c2434; }
      @media (max-width: 48em) {
        #index .collections__item a .label {
          bottom: 15px;
          left: 15px;
          padding: 5px 30px; } }
      #index .collections__item a .label span {
        display: inline-block;
        font-size: 1.6rem;
        color: #fff; }
        @media (max-width: 48em) {
          #index .collections__item a .label span {
            font-size: 1.3rem; } }
        #index .collections__item a .label span::after {
          content: attr(data-label-text);
          font-family: "Noto Sans JP", "ヒラギノ角ゴ Pro W3", "Hiragino Kaku Gothic Pro", Osaka, "游ゴシック", "Yu Gothic", YuGothic, "メイリオ", Meiryo, "ＭＳ Ｐゴシック", "MS PGothic", sans-serif;
          font-size: 0; }
    #index .collections__item a:hover .image img {
