          padding: 5px 30px 6px; } }
      #index .collections__item a:hover .label span {
        font-size: 0; }
        #index .collections__item a:hover .label span::after {
          font-size: 1.5rem; }
          @media (max-width: 48em) {
            #index .collections__item a:hover .label span::after {
              font-size: 1.2rem; } }

@media (max-width: 48em) {
